.gallery h1 {
  font-size: 48px;
  text-align: center;
  color: #0b1827;
}

.model {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000000;
  transition: opacity 0.4s ease, visibility 0.4s ease,
    transform 0.5s ease-in-out;
  visibility: hidden;
  opacity: 0;
  transform: scale(0);
  overflow: hidden;
  z-index: 999;
}

.model.open {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}

.model img {
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
  display: block;
  line-height: 0;
  box-sizing: border-box;
  padding: 20px 0 20px;
  margin: 0 auto;
}

.model.open svg {
  position: fixed;
  top: 10px;
  right: 10px;
  width: 2rem;
  height: 2rem;
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.4);
  color: #ffffff;
  cursor: pointer;
}

.gallery__image {
  column-count: 4;
  column-width: 33%;
  padding: 0;
}

.gallery__image img {
  width: 100%;
}

.pics {
  -webkit-transition: all 350ms ease;
  transition: all 350ms ease;
  cursor: pointer;
  margin-bottom: 12px;
}

.pics:hover {
  filter: opacity(0.8);
}

@media (max-width: 991px) {
  .gallery__image {
    column-count: 2;
  }
}

@media (max-width: 480px) {
  .gallery h1 {
    font-size: 36px;
  }

  .gallery__image {
    column-count: 1;
    column-width: 100%;
  }
}
